
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  padding: 20px;
  margin: 10px;
}

.width-define {
  width: 200px;
  height: 200px;
  border: 1px solid black;
  padding: 10px;
  overflow: hidden;
}

.first-ele {
  display: flex;
  flex-direction: row;
  gap: 20px;
  margin: 20px;
  text-align: center;

}

.react-pic {
  background: url(./images/react_pic.png) no-repeat;
  background-size: 100%;
  padding-top: 20px;
}

.javascript-pic {
  background: url(./images/js_pic.png) no-repeat;
  background-size: 100%;
}

.bootstrap-pic {
  background: url(./images/bootstrap-600x600.jpg) no-repeat;
  background-size: 100%;
}

.react-dom {
  font-size: 20px;
  margin: 0px auto;
  text-align: center;
  font-size: 30px;
}

.React-dom-color {
  background-color: rgb(25, 146, 216);
  font-weight: bold;
  color: azure;
}

.bg-light {
  margin-bottom: 20px;
}

.div-cont {
  text-align: center;
}

.form-div {
  border: 1px solid grey;
  padding: 15px;
}
